import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { DatabaseService } from 'src/app/services/database.service';

@Component({
  selector: 'app-student-details',
  templateUrl: './student-details.component.html',
  styleUrls: ['./student-details.component.scss']
})
export class StudentDetailsComponent implements OnInit {

  student : any;
  Ref : any;
  Users : any;
  

  constructor(public _data: DatabaseService, public _route: ActivatedRoute) { }

  //delete user
  deleteUser(){

    this.Ref = this._route.snapshot.paramMap.get('ref');
    this._data.deletestudent(this.Ref);
    
  }

  //update user
  updateUser(userInfo: NgForm){
   
    
    this.Ref = this._route.snapshot.paramMap.get('ref');
 


  this._data.updatestudent(userInfo.value,this.Ref).then(ref =>{
    alert("successfully updated")
  })
  }

  ngOnInit() {

    //get reference 

    this.Ref = this._route.snapshot.paramMap.get('ref');
    console.log ( 'Ref:',this.Ref);

    this._data.GetUsers().snapshotChanges().subscribe(action =>{
      console.log(action);
      this.student = action;
    });

    //single data

    this.student = this._data.getStudentInfo(this.Ref).subscribe((i) =>{
      this.student = i;
      console.log(this.student);
    })

    //getting single user

   
   

   


  }

}
